
.entryContainer {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: rgb(179, 182, 182);
  margin-top: -8px;
  margin-left: -8px;
  }
  
  .title {
    font-size: 28px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .formGroup {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 20px;
  }
  
  .label {
    font-size: 18px;
    margin-bottom: 5px;
  }
  
  .input {
    padding: 10px;
    border-radius: 5px;
    border: none;
    background-color: #f2f2f2;
    font-size: 16px;
    width: 100%;
    max-width: 400px;
  }
  
  .btnSubmit {
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 10px;
    font-size: 18px;
    cursor: pointer;
  }
  
  .btnSubmit:hover {
    background-color: #0069d9;
  }
  
  .register{
    cursor: pointer;
  }

  .register:hover {
    color: #0069d9;
  }